import React from 'react';
import artists from './data/db'
import Albums from './Albums';

const ArtistsPage = (props) => {

    let arr = artists.find(el => el.id === parseInt(props.match.params.id))
    return (
        <div className="ArtistsPage">
            <img src={require(`./data/images/covers/${arr.cover}.jpg`)} />
            <h4>{arr.name}</h4>
            <p>{arr.bio}</p>
            <div className="Albums row">
                {arr.albums.map((el, i) => (
                    <div key={i} className="col-md-6 col-6">
                        <Albums cover={el.cover}/>
                    </div>
                ))}
            </div>
        </div>
    );
}

export default ArtistsPage;
