import React from 'react';
import { Link } from 'react-router-dom';

const BrowseArtists = (props) => {
    return (
        <Link to={`/browse/${props.id}`}>
            <div className="BrowseArtists">
                <img src={require(`./data/images/covers/${props.cover}.jpg`)} />
                <h4>{props.name}</h4>
            </div>
        </Link>
    );
}


export default BrowseArtists;