import React from 'react';
import './App.css';
import "bootstrap/dist/css/bootstrap.min.css";
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import Header from './components/Header';
import Browse from './components/Browse';
import ArtistsPage from './components/ArtistsPage';
import artists from './components/data/db'

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { artists: artists }
  }

  render() {
    return (
      <Router>
        <div className="App col-md-4 offset-md-4">
          <Header />
          <Switch>
            <Route path="/" exact render={() => <Browse artists={this.state.artists} />} />
            <Route path="/browse/:id" component={ArtistsPage} />
          </Switch>
        </div>
      </Router>
    );
  }
}


export default App;
